import { Chart } from 'chart.js';





export default {
  name: 'CardGithubProyects',
  props: {repo : { type: Object, default : ()=>{}}},
  data(){
    return{
      colors : [],
    }
  },

  mounted(){
    this.printChart()
  },

  methods: {
    getColorFrom(n) {
      const simbolos = "0123456789ABCDEF"
      let color = "#"
      const colors = []
      for (let i = 0; i < n; i++) {
        color = "#"
        for (let j = 0; j < 6; j++) {color = color + simbolos[Math.floor(Math.random() * 16)]}
        colors.push(color)
      }
      return colors
    },

    printChart(){
      const values = Object.values(this.repo.data).length
      const colors = this.getColorFrom(values)
      this.colors = colors
      this.generateChart()
    },

    generateChart(){
      const ctx = document.getElementById(this.repo.name).getContext('2d');




      const myChart = new Chart(ctx, {
        type: 'doughnut',
        data: {
          labels: Object.keys(this.repo.data),
          datasets: [{
            data: Object.values(this.repo.data),
            backgroundColor: [
                'rgba(255, 99, 132, 0.5)',
                'rgba(54, 162, 235, 0.5)',
                'rgba(255, 206, 86, 0.5)',
                'rgba(75, 192, 192, 0.5)',
                'rgba(153, 102, 255, 0.5)',
                'rgba(255, 159, 64, 0.5)'
            ],
            borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)',
                'rgba(255, 206, 86, 1)',
                'rgba(75, 192, 192, 1)',
                'rgba(153, 102, 255, 1)',
                'rgba(255, 159, 64, 1)'],
            borderWidth: 1
          }]
        },
        options: {
          scales: {y: {beginAtZero: true}},
          plugins: {
            legend: {
              labels: {
                    // This more specific font property overrides the global property
                font: {
                  size: 20
                  }
              }
            },
            title: {
              text: "HOLLAA"
            }
          },        
        }
      })


        console.log(myChart)
        
        return myChart
    }
  }
}
